.Select-control {
    height: 35px;
}

.Select--currency {
    width: 100%;

    height: 35px;

    >div {
        border-radius: 0px;
        border-color: $light-grey !important;
    }

    @media(min-width: $min-sm) {
        width: 300px;
    }
}

.Select--option,
.Select-value {
    display: flex !important;
    align-items: center !important;

    img {
        background-color: white;
        width: 20px;
        margin-right: 10px;
        border-radius: 100%;
    }
}