.banner {
    display: flex;
    justify-content: space-between;
    background-color: $off-white;
    border-bottom: 1px solid $light-grey;
}

.banner--details {
    display: flex;
}

.banner--details span {
    padding-right: 10px;
    flex-shrink: 0;
    align-self: center;
}

.banner--details ul {
    display: inline-flex;
    align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
    overflow-x: scroll;
}

.banner--details ul::-webkit-scrollbar {
    /* remove scrollbar space */
    width: 0px;
    height: 0px;
}

.banner--details ul li {
    background-color: white;
    border-left: 1px solid $light-grey;
    flex-shrink: 0;
}

.banner--details ul li:last-child {
    border-right: 1px solid $light-grey;
}

.banner--details ul li a {
    display: inline-block;
    text-decoration: none;
    line-height: 35px;
    padding: 0 12px;
}

// .banner--details ul li a:hover {
//     text-decoration: underline;
// }

.banner--details ul+span {
    margin-left: 10px;
}

.banner--details img {
    width: 15px;
    margin-bottom: -3px;
    margin-right: 4px;
}