.wyre--button,
.kyber--button {
    padding: 0 10px 0 26px;
}

.wyre--button:before,
.kyber--button:before {
    content: '';
    background-size: cover;
    background-position: left;
    height: 16px;
    width: 12px;
    position: absolute;
    top: 50%;
    left: 8px;
    transform: translateY(-50%);
}

.wyre--button:before {
    background-image: url($path + 'wyre.svg');
}

.kyber--button:before {
    background-image: url($path + 'kyber.svg');
}