.popup.migration {
    max-width: 800px;
    padding: 0;
}

.migration>h1 {
    background: #001b3a;
    padding: 10px;
    color: white;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    margin-bottom: 0;
}

.migration--body {
    padding: 20px;
}

.migration--description {
    font-size: 18px;
}

.migration--frame {
    margin-top: 20px;
    text-align: left;
}

.migration--buttons {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;

    * {
        flex-grow: 1;
        flex-basis: 0;
    }

    *+* {
        margin-left: 10px;
    }
}

.migration--buttons .button--blue {
    background-color: #001b3a;
    color: white;
    border: none;
}

.migration--buttons .button--blue:focus {
    background-color: #0d3a6e;
}