.section.order {
    background-color: darken($off-white, 3%);
    border-bottom: 1px solid $light-grey;
    padding: 30px 0;
}

.order--currencies {
    display: flex;
    margin-bottom: 15px;

    @media (max-width: $min-sm) {
        flex-direction: column;
    }
}

.order--currencies .Select--currency+.Select--currency {
    margin-top: 10px;
    margin-left: 0px;

    @media (min-width: $min-sm) {
        margin-top: 0px;
        margin-left: 15px;
    }
}

.order--wrapper {
    background-color: white;
    border: 1px solid $light-grey;
    padding: 15px;
    position: relative;
}

.order--wrapper--tabbed {
    top: 40px;
    margin-bottom: 40px;
}

.order--wrapper p {
    line-height: 1.5;
    margin-bottom: 0;
}

.order--wrapper p+p {
    margin-top: 15px;
}

.order--wrapper p+button {
    margin-top: 10px;
}

.order--wrapper form>div+div {
    margin-top: 15px;
}

.order--tabs {
    display: flex;
    background-color: white;
    list-style: none;
    margin: 0;
    padding: 0;
    position: absolute;
    top: -40px;
    left: -1px;
}

.order--tabs li {
    display: flex;
    align-items: center;
    border: 1px solid $light-grey;
    height: 40px;
    width: 150px;
    text-align: center;
    cursor: pointer;
}

.order--tabs li:hover {
    background-color: $off-white;
}

.order--tabs li.active {
    border-bottom: none;
    pointer-events: none;
}

.order--tabs li+li {
    border-left: none;
}

.order--tabs li span {
    width: 100%;
}

.order--inputs {
    display: flex;

    @media (max-width: $min-md) {
        flex-direction: column;
        width: 50%;
    }

    @media (max-width: $min-sm) {
        flex-direction: column;
        width: 100%;
    }
}

.order--input-wrapper {
    flex-grow: 1;
    flex-basis: 0;
}

.order--title {
    margin-bottom: 5px;
    position: relative;
}

.order--title>span {
    font-weight: 500;
}

.order--option {
    text-decoration: underline;
    cursor: pointer;
}

.order--input-wrapper+.order--input-wrapper {
    margin-top: 10px;
    margin-left: 0px;

    @media (min-width: $min-md) {
        margin-left: 15px;
        margin-top: 0px;
    }
}

.order--input-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    border: 1px solid #cccccc;
    position: relative;
}

.order--input-item.disabled {
    background-color: $off-white;
}

.order--input-item:not(.disabled):hover {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.06);
}

.order--input-item>input[type="number"] {
    flex-grow: 1;
    background-color: transparent;
    border: none;
    height: 33px; // Subtracted 2px for additional border
}

.order--input-item>span {
    display: inline-block;
    padding-right: 10px;
    text-align: center;
}

.order--currency {
    margin-left: 5px;
}

.order--footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.order--options {
    display: flex;
    justify-content: flex-end;
    border: 1px solid #cccccc;
    border-top: 0px;
    padding: 15px;
    background-color: #f8fafb;

    &.hidden {
        display: none;
    }

    @media (max-width: $min-sm) {
        flex-direction: column;
    }
}

.order--options label+label {
    padding-left: 0px;
    padding-top: 5px;

    @media (min-width: $min-sm) {
        padding-left: 20px;
        padding-top: 0px;
    }
}

.order--button {
    background-color: $green;
    color: white;
    border: none;
    position: relative;
}

.order--button:before {
    content: "";
    background-color: rgba(0, 0, 0, 0.1);
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.2s ease-in-out;
}

.order--button:focus {
    background-color: $green;
}

.order--button:focus:before,
.order--button:hover:before {
    transform: scaleX(1);
    transform-origin: left;
}

.order--button:disabled {
    background-color: white;
    border: 1px solid $light-grey;
}

.order--button.submitting {
    &:disabled {
        background-color: $green;
        border: none;
    }

    .loading:after {
        border-color: white transparent white transparent;
    }
}

.order--button:disabled:hover:before {
    content: none;
}

.order--button span {
    position: relative;
    z-index: 1;
    pointer-events: none;
}

.order--input-wrapper .order--warning,
.order--input-wrapper .order--message {
    margin-top: 5px;
}

.order--warning,
.order--warning a {
    color: $red;
}