.trades--table table {
    // Fix scrolling
    width: calc(100% - 1px);
}

.trades--table.table--wrapper+* {
    margin-top: 15px;
}

.trades--table th {
    text-align: center;
}

.trades--table tr.recent {
    background-color: rgba($green, 0.3);
    animation: fadeHighlight 2s ease-in-out 1.2s forwards;
}

.trades--table tr.canceled td,
.trades--table tr.expired td {
    opacity: 0.6;
}

.trades--table td.volume,
.trades--table td.price,
.trades--table td.match {
    overflow: hidden;
    text-overflow: ellipsis;

    &:hover {
        text-overflow: clip;
    }
}

.trades--table td.volume {
    max-width: 120px;
}

.trades--table td.price {
    max-width: 220px;
}

.trades--table .cross {
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
}

.trades--table .loading:after {
    height: 15px;
    width: 15px;
    border-width: 1px;
}