.manage {
    background-color: darken($off-white, 3%);
    border-bottom: 1px solid $light-grey;
    padding: 30px 0;
}

.manage--row {
    display: flex;
    margin-top: 15px;
    @media(max-width: $min-sm) {
        flex-direction: column;
    }
}

.manage--row>*+* {
    margin-left: 0px;
    margin-top: 10px;
    @media(min-width: $min-sm) {
        margin-left: 15px;
        margin-top: 0px;
    }
}

.manage--title {
    @extend .order--title;
}

.manage--option {
    @extend .order--option;
}

.manage--input-item {
    @extend .order--input-item;
}

.manage--input-item input[type=number] {
    width: 143px;
}

.manage--input-item button {
    height: 33px; // Subtracted 2px for additional border
    border: none;
    border-left: 1px solid $light-grey;
}