.popup--outer {
    z-index: 10;
}

.overlay {
    display: block;
    background-color: rgba(140, 140, 140, 0.6);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    // z-index: 5;
}

.popup {
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 10px 25px 0 rgba(0, 0, 0, 0.2);
    text-align: center;
    padding: 25px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    // z-index: 10;
}

.popup h2,
.popup p {
    margin: 0;
}

.popup h2+* {
    margin-top: 10px;
}

.popup--buttons {
    margin-top: 15px;
}

.popup.open,
.popup.kyc {
    min-width: 300px;
}

.popup--buttons>*+* {
    margin-left: 15px;
}

.popup .popup--buttons+p {
    margin-top: 15px;
}

.popup.accounts h2 {
    text-align: center;
}

.popup.accounts .account--button {
    padding: 20px;
    width: 500px;
    height: 40px;
    box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.06);
    margin-bottom: 10px;
}

.popup.accounts .blocky {
    height: 40px;
}

.popup.accounts .account--left {
    width: 40px;
    height: 40px;
    padding: 0;
    margin: 0;
    margin-top: -20px;
    margin-left: -20px;
    float: left;
}

.popup.accounts .account--right {
    margin-top: -9px;
    padding-left: 20px;
    font-size: 15px;
    float: left;
}

.popup.keystore h2 {
    text-align: center;
}

.popup.keystore form {
    display: flex;
    flex-direction: column;
}

.popup.keystore form>*+* {
    margin-top: 15px;
}

.popup.keystore p {
    margin-top: 15px;
}

.popup.approve .approve--list {
    list-style: none;
    margin: 15px 0 0;
    padding: 0;
}

.popup.approve .approve--list li {
    position: relative;
}

.popup.approve .approve--list li+li {
    margin-top: 5px;
}

.popup.approve .approve--list span.active {
    font-weight: 500;
}

.popup.approve .approve--tick {
    font-size: 16px;
    color: $green;
    margin-left: 5px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.popup.terms button {
    margin-top: 10px;
}

.terms--content {
    background-color: $off-white;
    border: 1px solid $light-grey;
    max-height: 400px;
    padding: 15px;
    margin-bottom: 15px;
    text-align: left;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}

.terms--content>ol {
    margin: 10px 0 0;
}

.terms--content>ol,
.terms--content>ol>li>ol {
    counter-reset: item;
    padding-left: 30px;
}

.terms--content>ol>li,
.terms--content>ol>li>ol>li {
    display: block;
}

.terms--content>ol>li:before,
.terms--content>ol>li>ol>li:before {
    content: counters(item, ".") ". ";
    counter-increment: item;
}

.terms--content li {
    margin: 5px 0;
}

.terms--content p+p {
    margin-top: 20px;
}

.terms--content br {
    line-height: 2;
}

.popup {
    .approve--loading {
        margin-top: 15px;
    }

    .approve--close {
        margin-top: 15px;
    }

    .atom--error {
        margin-top: 10px;
        color: $red;
    }

    .atom--success {
        color: green;
    }
}

.popup.atom button {
    width: 100%;
    margin-top: 10px;
}

.popup .sign--data {
    margin-top: 30px;
    margin-bottom: 30px;
}

.popup .sign--datum {
    background-color: $off-white;
    border: 1px solid $light-grey;
    padding: 10px;
    color: #333;
}

.popup .sign--datum+.sign--datum {
    border-left: none;
}

.popup .sign--button {
    margin-top: 15px;
}

// Open Order
.open--tokens {
    display: flex;
    justify-content: center;
}

.open--tokens>div {
    position: relative;
    text-align: center;
}

.open--tokens p {
    white-space: nowrap;
}

.open--tokens .open--arrow {
    display: flex;
    align-items: center;
    height: 50px;
    margin: 0 15px;
    font-size: 20px;
    font-weight: bold;
}

.open--tokens img {
    background-color: white;
    border-radius: 100%;
    width: 50px;
    margin-bottom: 5px;
}

.popup.open .open--details {
    margin: 25px 0 15px;
}

.popup.open .open--details span {
    font-weight: 500;
}

.popup.open .open--confirm {
    @extend .order--button;
}

.popup.ledger button {
    width: 100%;
    margin-top: 10px;
}

.ledger--instructions ol {
    position: relative;
    margin: 10px 0;
    padding-left: 20px;
    list-style: none;
    counter-reset: counter;
    position: relative;
    text-align: left;
}

.ledger--instructions ol>li:before {
    font-size: 9px;
    font-weight: bold;
    display: inline-block;
    text-align: center;
    margin-right: 5px;
    counter-increment: counter;
    content: counter(counter);
}

.ledger--instructions ol>li+li {
    margin-top: 10px;
}

.ledger--instructions+p {
    margin-bottom: 15px;
}